import React, { useState, useCallback, useRef, useMemo } from 'react';
import ReactFlow, { 
    addEdge, 
    Background, 
    Controls, 
    useNodesState, 
    useEdgesState, 
    ReactFlowProvider, 
    MarkerType, 
    Handle, 
    Position 
} from 'reactflow';
import 'reactflow/dist/style.css';
import { useNavigate, useSearchParams } from 'react-router-dom';
import { Button } from '@/components/ui/button';
import { 
    ArrowLeft, 
    Save, 
    Play, 
    Settings, 
    Plus, 
    Mail, 
    Bot, 
    Megaphone, 
    UserPlus, 
    Inbox, 
    Layers, 
    ChevronRight, 
    Activity, 
    Shield, 
    Clock, 
    Zap, 
    Search,
    Phone,
    MessageSquare,
    Facebook,
    Database,
    FlaskConical,
    FileText,
    Share2,
    Users,
    ClipboardList,
    Globe,
    Lock
} from 'lucide-react';

// Enhanced Professional Node Component
const CustomNode = ({ data, selected }) => {
    const Icon = data.icon || Layers;
    const color = data.color || 'indigo';
    
    return (
        <div className={`group rounded-xl border transition-all duration-300 ${
            selected 
            ? 'border-indigo-500 shadow-[0_0_20px_rgba(99,102,241,0.3)] bg-[#12163a]' 
            : 'border-white/10 bg-[#0d1024]/90'
        } backdrop-blur-md min-w-[200px] overflow-hidden`}>
            {/* Top accent bar */}
            <div className={`h-1 w-full bg-${color}-500/50`} />
            
            <Handle type="target" position={Position.Top} className="!w-2 !h-2 !bg-indigo-500 !border-none !top-[-1px]" />
            
            <div className="p-3">
                {/* Header Section */}
                <div className="flex items-center justify-between mb-3">
                    <div className="flex items-center space-x-2">
                        <div className={`p-1.5 rounded-md bg-${color}-500/10 border border-${color}-500/20`}>
                            <Icon size={12} className={`text-${color}-400`} />
                        </div>
                        <div className="flex flex-col">
                            <span className="text-[10px] font-bold text-white uppercase tracking-wider">{data.label}</span>
                            <span className="text-[8px] text-white/30 uppercase tracking-tight">{data.category || 'Service Control'}</span>
                        </div>
                    </div>
                    {data.status && (
                        <div className="flex items-center space-x-1 bg-emerald-500/10 px-1.5 py-0.5 rounded-full border border-emerald-500/20">
                            <div className="w-1 h-1 rounded-full bg-emerald-500 animate-pulse" />
                            <span className="text-[8px] font-bold text-emerald-400">ONLINE</span>
                        </div>
                    )}
                </div>

                {/* Configuration Snippet */}
                <div className="space-y-1.5 mb-3 bg-white/[0.02] p-2 rounded-lg border border-white/5">
                    {data.config?.map((item, i) => (
                        <div key={i} className="flex items-center justify-between">
                            <span className="text-[9px] text-white/30">{item.key}:</span>
                            <span className="text-[9px] text-white/60 font-mono truncate max-w-[100px]">{item.value}</span>
                        </div>
                    ))}
                    {!data.config && (
                        <div className="flex flex-col items-center justify-center py-2 opacity-20">
                            <Settings size={10} className="mb-1" />
                            <span className="text-[8px]">NO CONFIG</span>
                        </div>
                    )}
                </div>

                {/* Footer Metrics */}
                <div className="flex items-center justify-between pt-2 border-t border-white/5">
                    <div className="flex items-center space-x-2 text-white/30">
                        <Activity size={10} />
                        <span className="text-[8px]">{data.metrics?.execution || '0ms'}</span>
                    </div>
                    <div className="flex items-center space-x-1 text-white/30">
                        <Shield size={10} />
                        <span className="text-[8px]">{data.metrics?.uptime || '100%'}</span>
                    </div>
                    <div className="flex items-center space-x-1 text-white/30">
                        <Clock size={10} />
                        <span className="text-[8px]">{data.metrics?.lastRun || 'N/A'}</span>
                    </div>
                </div>
            </div>
            
            <Handle type="source" position={Position.Bottom} className="!w-2 !h-2 !bg-indigo-500 !border-none !bottom-[-1px]" />
        </div>
    );
};

const nodeTypes = {
    custom: CustomNode,
};

const initialNodes = [
    {
        id: '1',
        type: 'custom',
        data: { 
            label: 'Automation Gateway', 
            category: 'GATEWAY',
            icon: Zap,
            color: 'indigo',
            status: true,
            config: [
                { key: 'Trigger', value: 'API_HOOK' },
                { key: 'Env', value: 'Production' }
            ],
            metrics: { execution: '8ms', uptime: '99.9%', lastRun: '1m' }
        },
        position: { x: 400, y: 100 },
    },
];

const initialEdges = [];

const CreateWorkflowCanvas = () => {
    const navigate = useNavigate();
    const [searchParams] = useSearchParams();
    const workflowName = searchParams.get('name') || 'Untitled Workflow';
    
    const reactFlowWrapper = useRef(null);
    const [nodes, setNodes, onNodesChange] = useNodesState(initialNodes);
    const [edges, setEdges, onEdgesChange] = useEdgesState(initialEdges);
    const [reactFlowInstance, setReactFlowInstance] = useState(null);

    const onConnect = useCallback((params) => {
        setEdges((eds) => addEdge({
            ...params,
            type: 'smoothstep',
            animated: true,
            markerEnd: { type: MarkerType.ArrowClosed, color: '#5a6eff' },
            style: { stroke: '#5a6eff', strokeWidth: 1.5 },
        }, eds));
    }, []);

    const onDragStart = (event, item) => {
        event.dataTransfer.setData('application/reactflow', 'custom');
        event.dataTransfer.setData('application/data', JSON.stringify(item));
        event.dataTransfer.effectAllowed = 'move';
    };

    const onDragOver = useCallback((event) => {
        event.preventDefault();
        event.dataTransfer.dropEffect = 'move';
    }, []);

    const onDrop = useCallback(
        (event) => {
            event.preventDefault();
            const reactFlowBounds = reactFlowWrapper.current.getBoundingClientRect();
            const rawData = event.dataTransfer.getData('application/data');
            if (!rawData) return;
            const dropData = JSON.parse(rawData);
            const position = reactFlowInstance.project({
                x: event.clientX - reactFlowBounds.left,
                y: event.clientY - reactFlowBounds.top,
            });
            const newNode = {
                id: `node_${Date.now()}`,
                type: 'custom',
                position,
                data: { ...dropData, status: false, metrics: { execution: '—', uptime: '100%', lastRun: 'Pending' } },
            };
            setNodes((nds) => nds.concat(newNode));
        },
        [reactFlowInstance, nodes]
    );

    const automationNodes = [
        {
            category: "Communication Hub",
            items: [
                { 
                    label: "Gmail: Outreach", category: "EMAIL", icon: Mail, color: "red",
                    config: [{ key: 'Service', value: 'OAuth_Gmail' }, { key: 'Template', value: 'Outreach_V1' }]
                },
                { 
                    label: "SMS: Notify", category: "MESSAGING", icon: MessageSquare, color: "blue",
                    config: [{ key: 'Provider', value: 'Twilio' }]
                },
                { 
                    label: "FB: Messenger", category: "SOCIAL", icon: Facebook, color: "blue",
                    config: [{ key: 'App', value: 'MetaBiz' }]
                },
                { 
                    label: "Voice: Call", category: "VOIP", icon: Phone, color: "emerald",
                    config: [{ key: 'Route', value: 'Global_SD' }]
                }
            ]
        },
        {
            category: "AI & Intelligence",
            items: [
                { 
                    label: "Agent: Deploy", category: "NEURAL", icon: Bot, color: "indigo",
                    config: [{ key: 'Model', value: 'GPT-4o' }, { key: 'Mood', value: 'Helpful' }]
                },
                { 
                    label: "Knowledge: Query", category: "DATABASE", icon: Database, color: "amber",
                    config: [{ key: 'Vector', value: 'Pinecone' }]
                },
                { 
                    label: "Transcribe: RT", category: "AUDIO", icon: Activity, color: "cyan",
                    config: [{ key: 'Lang', value: 'Auto' }]
                },
                { 
                    label: "Analysis: Lead", category: "PROCESSING", icon: Search, color: "purple",
                    config: [{ key: 'Depth', value: 'Level_3' }]
                }
            ]
        },
        {
            category: "Clinical Ecosystem",
            items: [
                { 
                    label: "Study: Create", category: "CLINICAL", icon: FlaskConical, color: "emerald",
                    config: [{ key: 'Phase', value: 'II' }]
                },
                { 
                    label: "Participant: Enroll", category: "PATIENT", icon: Users, color: "emerald",
                    config: [{ key: 'Criteria', value: 'Strict' }]
                },
                { 
                    label: "Site: Monitor", category: "CLINICAL", icon: Globe, color: "sky",
                    config: [{ key: 'Location', value: 'Remote' }]
                },
                { 
                    label: "Clinical: Review", category: "AUDIT", icon: ClipboardList, color: "rose",
                    config: [{ key: 'Standard', value: 'HIPAA' }]
                }
            ]
        },
        {
            category: "Sales & Marketing",
            items: [
                { 
                    label: "Leads: Scrape", category: "EXTRACTION", icon: Share2, color: "orange",
                    config: [{ key: 'Source', value: 'LinkedIn' }]
                },
                { 
                    label: "Campaign: Launch", category: "OUTREACH", icon: Megaphone, color: "pink",
                    config: [{ key: 'Budget', value: 'Dynamic' }]
                },
                { 
                    label: "Ads: Management", category: "MARKETING", icon: Globe, color: "indigo",
                    config: [{ key: 'Platform', value: 'Google' }]
                },
                { 
                    label: "Contact: Update", category: "CRM", icon: UserPlus, color: "amber",
                    config: [{ key: 'Priority', value: 'High' }]
                }
            ]
        },
        {
            category: "System & Core",
            items: [
                { 
                    label: "Webhook: Push", category: "GATEWAY", icon: Zap, color: "yellow",
                    config: [{ key: 'Secure', value: 'Hmac_V2' }]
                },
                { 
                    label: "Audit: Log", category: "SECURITY", icon: Lock, color: "slate",
                    config: [{ key: 'Store', value: 'Encrypted' }]
                },
                { 
                    label: "File: Process", category: "UTILITY", icon: FileText, color: "blue",
                    config: [{ key: 'Type', value: 'PDF_OCR' }]
                },
                { 
                    label: "Delay: Wait", category: "LOGIC", icon: Clock, color: "slate",
                    config: [{ key: 'Wait', value: '5m' }]
                }
            ]
        }
    ];

    return (
        <ReactFlowProvider>
            <div className="app-shell-bg min-h-screen flex flex-col font-sans text-white">
                {/* Minimal Topbar */}
                <div className="h-14 border-b border-white/5 flex items-center justify-between px-6 bg-[#0b0f2e]/90 backdrop-blur-xl z-20">
                    <div className="flex items-center space-x-4">
                        <button onClick={() => navigate('/workflows')} className="p-2 hover:bg-white/5 rounded-lg transition-colors">
                            <ArrowLeft size={18} className="text-white/60" />
                        </button>
                        <div className="flex flex-col">
                            <span className="text-[10px] text-white/30 uppercase tracking-[0.2em] font-semibold">Architect Engine</span>
                            <span className="text-sm font-medium tracking-tight truncate max-w-[200px]">{workflowName}</span>
                        </div>
                    </div>

                    <div className="flex items-center space-x-2">
                        <div className="hidden lg:flex items-center space-x-1 bg-white/5 rounded-md px-2 py-1 mr-4 border border-white/10">
                            <Activity size={12} className="text-indigo-400" />
                            <span className="text-[10px] text-white/50 uppercase font-bold tracking-tighter">Cluster: Global-West-1</span>
                        </div>
                        <Button variant="ghost" size="sm" className="text-[11px] h-8 text-white/60 hover:text-white uppercase tracking-widest">
                            <Settings size={14} className="mr-2" />
                            Config
                        </Button>
                        <div className="w-px h-4 bg-white/10 mx-2" />
                        <Button size="sm" className="bg-indigo-600 hover:bg-indigo-500 h-8 text-[11px] px-4 rounded-md shadow-lg shadow-indigo-500/20 uppercase tracking-widest font-bold">
                            <Play size={12} className="mr-2 fill-current" />
                            Deploy
                        </Button>
                    </div>
                </div>

                <div className="flex-1 flex overflow-hidden" ref={reactFlowWrapper}>
                    {/* Professional Sidebar */}
                    <div className="w-80 border-r border-white/5 bg-[#0b0f2e]/50 backdrop-blur-sm flex flex-col z-10">
                        <div className="p-5 flex-1 overflow-y-auto space-y-8 scrollbar-hide">
                            {automationNodes.map((cat, idx) => (
                                <div key={idx} className="space-y-4">
                                    <h3 className="text-[9px] font-bold text-white/30 uppercase tracking-[0.15em] ml-1">{cat.category}</h3>
                                    <div className="space-y-2">
                                        {cat.items.map((item, i) => (
                                            <div 
                                                key={i}
                                                draggable
                                                onDragStart={(e) => onDragStart(e, item)}
                                                className="group p-3 rounded-xl border border-white/5 bg-white/[0.01] hover:bg-white/[0.04] hover:border-indigo-500/30 transition-all cursor-grab active:cursor-grabbing"
                                            >
                                                <div className="flex items-center justify-between mb-2">
                                                    <div className="flex items-center space-x-3">
                                                        <div className={`p-1.5 rounded-lg bg-${item.color}-500/10 group-hover:bg-${item.color}-500/20 transition-colors`}>
                                                            {React.createElement(item.icon, { size: 14, className: `text-${item.color}-500` })}
                                                        </div>
                                                        <div className="flex flex-col">
                                                            <span className="text-[11px] text-white/70 group-hover:text-white transition-colors font-bold uppercase tracking-tight">{item.label}</span>
                                                            <span className="text-[8px] text-white/20 uppercase">{item.category}</span>
                                                        </div>
                                                    </div>
                                                    <Plus size={10} className="text-white/10 group-hover:text-indigo-400 transition-colors" />
                                                </div>
                                            </div>
                                        ))}
                                    </div>
                                </div>
                            ))}
                        </div>
                    </div>

                    {/* Pro Canvas */}
                    <div className="flex-1 relative bg-[#090b1a]">
                        <ReactFlow
                            nodes={nodes}
                            edges={edges}
                            onNodesChange={onNodesChange}
                            onEdgesChange={onEdgesChange}
                            onConnect={onConnect}
                            onInit={setReactFlowInstance}
                            onDrop={onDrop}
                            onDragOver={onDragOver}
                            nodeTypes={nodeTypes}
                            defaultEdgeOptions={{ type: 'smoothstep' }}
                            fitView
                        >
                            <Background color="#1e2246" gap={30} variant="lines" size={1} />
                            <Controls 
                                className="!bg-[#0d1024] !border-white/10 !shadow-2xl !bottom-6 !left-6" 
                                showInteractive={false}
                            />
                        </ReactFlow>
                    </div>
                </div>
            </div>
            <style dangerouslySetInnerHTML={{ __html: `
                .react-flow__handle { width: 4px !important; height: 4px !important; background: #5a6eff !important; border: none !important; }
                .react-flow__controls-button { border-bottom: 1px solid rgba(255, 255, 255, 0.05) !important; background: #0d1024 !important; fill: #ffffff66 !important; }
                .react-flow__controls-button:hover { background: #1a1f4d !important; fill: white !important; }
                .react-flow__attribution { display: none; }
                .scrollbar-hide::-webkit-scrollbar { display: none; }
                .react-flow__edge-path { stroke-width: 1.5 !important; stroke: rgba(90, 110, 255, 0.3) !important; }
            `}} />
        </ReactFlowProvider>
    );
};

export default CreateWorkflowCanvas;
